import React from 'react';

const NotFoundPage = () => (
  <div>
    <h1>Woops, something went wrong.</h1>
    <p>You just hit a route that doesn&#39;t exist, but no worries. We&#39;re redirecting you to <b>docs.sixgill.com</b></p>
  </div>
);

export default NotFoundPage;


